import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { RestaurantsComponent } from './restaurants.component';
import { AuthGuard } from '../shared/guard';
import { Auth2Guard } from '../shared/guard';
import { Auth3Guard } from '../shared/guard';
const routes: Routes = [
    {
        path: '',
        component: RestaurantsComponent,
        children: [
            { path: '', redirectTo: 'admin' },
            { path: 'admin', loadChildren: './admin/admin.module#AdminModule',canActivate: [AuthGuard]},
            { path: 'waiter', loadChildren: './waiter/waiter.module#WaiterModule',canActivate: [Auth2Guard]},
            { path: 'kitchen', loadChildren: './kitchen/kitchen.module#KitchenModule',canActivate: [Auth3Guard]}
        ]}
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class RestaurantsRoutingModule { }